import React, {useContext, useEffect, useState} from "react";
import { Navbar, Button, Nav } from "react-bootstrap";
import { faGithub, faLinkedin } from "@fortawesome/free-brands-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Link } from "react-router-dom";
import { FirebaseContext } from "../config/firebase";

class Header extends React.Component {
  static contextType = FirebaseContext;
  constructor(props) {
    super(props);
    this.state = {user: props.user};
    this.showUser = this.showUser.bind(this);
  }

  showUser(e) {
    e.preventDefault();

    console.log(this.context.user);
    // if (this.context.user)
    // {
    //   this.setState({user: this.context.user.email});
    // }
  }

  componentDidUpdate() {
    console.log("Update");
    if (this.context.user)
    {
      this.setState({user: this.context.user});
    }
  }

  render() {
  
  // const [user, setUser] = useState(null);
  
  
  
  return (
    <div>
      <Navbar className="navbar-background" expand="lg" variant="dark">
        <Navbar.Brand as={Link} to="./home" className="navbar-name">
          Eoin O'Neill
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="mr-auto">
            <Nav.Link as={Link} to="./home" className="navbar-text">
              Home
            </Nav.Link>
            <Nav.Link as={Link} to="./work" className="navbar-text">
              Work
            </Nav.Link>
            <Nav.Link as={Link} to="./contact" className="navbar-text">
              Contact Me
            </Nav.Link>
            
              {(!this.state.user) ? "" : <Nav.Link>{this.state.user.email}</Nav.Link>}
            
            {/* <Button variant="primary" type="submit" style={{ marginLeft: '.25rem'}} onClick={this.showUser}>
              Show User
            </Button> */}
            {/* <Nav.Link as={Link} to="/about" className="navbar-text">
              About Me
            </Nav.Link> */}
          </Nav>
          <div className="navbar-right">
            <div className="navbar-icons">
              <a
                href="https://github.com/e-oneill"
                rel="noreferrer"
                target="_blank"
              >
                <FontAwesomeIcon
                  icon={faGithub}
                  size={"lg"}
                  className="navbar-icon"
                />
              </a>
              <a
                href="https://www.linkedin.com/in/eoin-o-neill-785768134/"
                rel="noreferrer"
                target="_blank"
              >
                <FontAwesomeIcon
                  icon={faLinkedin}
                  size={"lg"}
                  className="navbar-icon"
                />
              </a>
            </div>

            <Button
              href="./files/IT_CV.pdf"
              variant="success"
              size="large"
              target="_blank"
            >
              CV/Resume
            </Button>
          </div>
        </Navbar.Collapse>
      </Navbar>
    </div>
  );
  }
};



export default Header;
