import React, {useRef} from "react"
import {Container, Card, Form, Button} from "react-bootstrap"
import { FirebaseContext } from "../config/firebase";

class SignIn extends React.Component {
  constructor(props)
  {
    super(props);
    this.state = {
      email: "",
      password: ""
    }
    this.handleSubmit = this.handleSubmit.bind(this);
    this.showUser = this.showUser.bind(this);
  }

  async handleSubmit(e) {
    e.preventDefault()

    await this.context.api.signIn(this.state.email, this.state.password);
    // this.props.updateUser(this.context.user);
    
  }

  showUser(e) {
    e.preventDefault()
    // this.props.updateUser();
    // console.log(this.context.user);
    console.log(this.props.updateUser);
  }
  static contextType = FirebaseContext
  render () {
    // const userUpdate = this.props.updateUser;
    return (
      <Container style={{display: 'flex', padding: '1rem', alignItems: 'center'}}>
        <Card style={{ width: '18rem', padding: '.75rem' }}>
          <Form>
            <Form.Group className="mb-3" controlId="formBasicEmail">
              <Form.Label>Email address</Form.Label>
              <Form.Control type="email" placeholder="Enter email" onChange={e => this.setState({email: e.target.value})} required/>
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicPassword">
              <Form.Label>Password</Form.Label>
              <Form.Control type="password" placeholder="Password" onChange={e => this.setState({password: e.target.value})} required/>
            </Form.Group>
            <Button variant="primary" type="submit" onClick={this.handleSubmit}>
              Submit
            </Button>
            <Button variant="primary" style={{ marginLeft: '.25rem'}} onClick={this.props.updateUser}>
              Show User
            </Button>
          </Form>
        </Card>
        
      </Container>
    )
  }
}

export default SignIn;