import "bootstrap/dist/css/bootstrap.min.css";

import "./styles.css";
import "./app.css";
import React, { useEffect } from "react";
import { Switch, Route, HashRouter } from "react-router-dom";
import Header from "./components/Header";
import Home from "./components/Home";
import Contact from "./components/Contact";
import About from "./components/About";
import Work from "./components/Work";
import {FirebaseContext} from "./config/firebase";
import SignIn from "./components/SignIn";

class App extends React.Component {
  static contextType = FirebaseContext;

  constructor(props) {
    super(props)
    this.state = {baseUrl: "", user: null}
    this.updateUser = this.updateUser.bind(this);
  }

  componentDidMount() {
    document.title="Eoin O'Neill's Portfolio";
    this.setState({user: this.context.user});
    console.log("Component Did Mount");
    // console.log(FirebaseContext.baseUrl);
    // console.log(await this.context.api.getProjects());
    // if (this.context.user !== null && this.state.user !== this.context.user)
    // {
    //   this.setState({user: this.context.user})
    // }
  }

  updateUser(e) {
    e.stopPropagation();
    console.log("Update Function Called");
    // if (this.context.user)
    // {
    //   if (!this.state.user)
    //   {
    //   this.setState({user: this.context.user})
    //   console.log(this.state.user);
    //   }    
    // }  
  }

  render() {
  return (
    <div className="App">
      <HashRouter>
        <Header user={this.state.user}/>
        <Switch>
          <Route path="/contact" component={Contact} />
          <Route path="/home" component={Home} />
          <Route path="/work" component={Work} baseUrl={this.state.baseUrl} />
          <Route path="/about" component={About} />
          <Route path="/csb-k8ebk" component={Home} />
          <Route path="/signin" component={() => < SignIn updateUser={this.updateUser}/>} />
          <Route path="" exact component={Home} />
        </Switch>
      </HashRouter>
    </div>
  );
  }
}

export default App;
