//this contains the firebase context and provider
import React, { createContext, useEffect } from "react";
import firebaseConfig from "./firebaseConfig";
import { initializeApp, getApps } from "firebase/app";
import { getAuth, createUserWithEmailAndPasswor, signInWithEmailAndPassword   } from "firebase/auth";
import {
  getDatabase,
  ref,
  DataSnapshot,
  onValue,
  get,
  child,
  set
} from "firebase/database";

const FirebaseContext = createContext(null);
export { FirebaseContext };

export default ({ children }) => {
  let firebase = {
    app: null,
    auth: null,
    database: null
  };

  //check if firebase app has been init
  if (!getApps.length) {
    const app = initializeApp(firebaseConfig);
    const auth = getAuth(); 
    // console.log(auth);
    firebase = {
      app: app,
      auth: auth,
      user: null,
      database: getDatabase(app),
      api: {
        getProjects,
        signIn
      }
    };
  }
  const db = firebase.database;
  const dbRef = ref(db);
  //function to query lists from the database
  function getProjects() {
    const response = get(child(dbRef, `projectList`))
      .then((snapshot) => {
        if (snapshot.exists()) {
          const listsObj = snapshot.toJSON();
          let listsArr = Object.keys(listsObj).map((key) => listsObj[key]);
          return listsArr;
        } else {
          return "No data available";
        }
      })
      .catch((error) => {
        return error;
      });

    return response;
  }

  function signIn(email,password) {
    console.log("Sign in pressed");
    signInWithEmailAndPassword(firebase.auth,email,password)
    .then((userCredential) => {
      //Signed in
      const user = userCredential.user;
      firebase.user = user
      // console.log(firebase.user);
    })
    .catch((error) => {
      const errorCode = error.code;
      const errorMessage = error.message;
      console.log("Error " + errorCode + ": " + errorMessage); 
    })
  }
  


  return (
    <FirebaseContext.Provider value={firebase}>
      {children}
    </FirebaseContext.Provider>
  );
};