import React from "react";
import { Jumbotron } from "react-bootstrap";

const Index_Jumbotron = () => {
  return (
    <Jumbotron fluid>
        <h1>Full-Stack Software and Web Development</h1>
        <h3>MySQL, REST APIs, Java, PHP, JavaScript, React, CSS</h3>
        <div className="jumbotron_text">
        <p>
        I'm graduating from Maynooth University in June 2022, 
        and am looking for a junior development position. I have learned and worked with all of the technologies listed above and am always open to learning new technologies and frameworks.
        </p>
        </div>
    </Jumbotron>
  );
};

export default Index_Jumbotron;
