import React from "react";
import ContactUs from "./ContactUs";
import { Container, Card} from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faLinkedin } from "@fortawesome/free-brands-svg-icons";
import { faEnvelope } from '@fortawesome/free-solid-svg-icons'
class Contact extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      email: '',
      message: ''
    } 

    this.onNameChange = this.onNameChange.bind(this);
    this.onEmailChange = this.onEmailChange.bind(this);
    this.onMessageChange = this.onMessageChange.bind(this);
  }
  


  resetForm(){
    this.setState({name: '', email: '', message: ''})
  }

  render () {


    return (
      <div className="about_container">
      <script type="text/javascript" src="https://cdn.jsdelivr.net/npm/emailjs-com@3/dist/email.min.js"></script>
        <div className="row">
          <div className="col-sm-12 col-md-6">
          <h1>Getting in touch</h1>
            <p className="contact-text">
              If you want to contact me about an opportunity or work, the best way to get in touch is over LinkedIn or Email.
            </p>
            <Card className="contact-card linkedin">
              <a href="https://www.linkedin.com/in/eoin-o-neill-785768134/" className="contact-link" rel="noreferrer" target="_blank">
              <Card.Body>
              <FontAwesomeIcon
                  icon={faLinkedin}
                  size={"lg"}
                  className="contact-symbol"
                />
                <span className="contact-card-text">LinkedIn</span>
              </Card.Body>
              </a>
            </Card>
            <Card className="contact-card email">
              <a href="mailto:eoneill@live.co.uk" className="contact-link" rel="noreferrer" target="_blank">
              <Card.Body>
              <FontAwesomeIcon
                  icon={faEnvelope}
                  size={"lg"}
                  className="contact-symbol"
                />
                <span className="contact-card-text">Email</span>
              </Card.Body>
              </a>
            </Card>
            <p className="contact-text">
              If you want to get in touch about one of my projects, the best way to do it is on the github page that is linked on each project's card.
            </p>
          </div>
          <div className="col-sm-12 col-md-6">
            <Container>
              <h2>Send me a message</h2>
              <ContactUs />
            </Container>
          </div>
        </div>
      </div>
    );
  };

  

  onNameChange(event) {
    this.setState({name: event.target.value})
  }

  onEmailChange(event) {
    this.setState({email: event.target.value})
  }

  onMessageChange(event) {
    this.setState({message: event.target.value})
  }

  

}

export default Contact;
